.footer{
    padding: 50px 0px;
    background-color: #eeeaff;
    width: 100%;
    margin-top: auto;
}


.footer-wrapper{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    column-gap: 3rem;
    background-color: #eeeaff;
  margin-left: 90px;
}

.footer-box{
    width: 30%;
}

.footer-box p{
    line-height: 1.2rem;
    margin-top: 20px;
}
.footer_title{
    font-size: 1.2rem;
    margin-bottom: 20px;
    margin-left: 35px;
}
 .footer_links li{
   line-height: 40px;
   color: var(--heading-color);
}
.footer_links li a{
    cursor: pointer;

}
.footer_links li a:hover{
    color: var(--primary-color);
}